<template>
  <div class="main-body">
    <!-- <h3>{{msg}}</h3>
    <h3>{{order}}</h3> -->
    <div class="main">
      <!-- <div class="header-title">
          订单
      </div> -->
      <div class="carousel">
        <div class="inside-msg">
          <h3>当前餐桌号：2</h3>
          <h3>优惠信息：暂无</h3>
          <h3>订单总计：￥ {{totalCount}}</h3>
        </div>
        <div class="wrapper">
          <!-- <img src="/static/img/季季红背景.jpg" alt=""> -->
        </div>
      </div>
      <div class="order">
        <div class="goods" v-for="(order, index1) in list" :key="index1" :class="{add: index1 !== 0}">
          <div class="good"
            @click="changeIndex(index1, index2)"
            :class="{active: activeIndex1 === index1 && activeIndex2 === index2}"
            v-for="(food, index2) in order"
            :key="index2">
            <div class="img float-left">
              <img :src="food.image" alt="">
            </div>
            <div class="msg float-left">
              <div>
                <span class="name float-left">{{food.name}}</span>
                <span class="single-count float-right" >￥{{food.count * food.price}}</span>
              </div>
              <!-- <div class="price">单价：{{food.price}}</div> -->
              <div class="count"> × {{food.count}} </div>
              </div>
          </div>
        </div>
        <div class="count" v-if="list.length === 0">
          暂无
        </div>
      </div>
      <div class="total-count" v-if="list.length !== 0">
        <div class="count">合计：{{totalCount}}</div>
        <div class="pay-order icon-daifukuanicon" @click="payOrder"> 支付订单</div>
      </div>
    </div>
    <div class="pay-for" v-if="showPay">
      <div class="pay-main">
        <div class="pay-method" v-if="activeStep === 0">
          <div class="pay-message">支付方式： 需支付 ￥{{totalCount}} </div>
          <!-- <div class="pay-message">支付方式：支付方式只可选择一次</div> -->
          <div class="flex-pay">
            <div class="pay"
              v-for="(item ,index) in payMethods"
              :key="index"
              :class= "{ activePay:payMethod === item.method}"
              @click="changeMethod(index)">
              <span :class="item.icon"></span>
              {{item.method}}
              <div :class="{'el-icon-success':payMethod === item.method}"></div>
            </div>
          </div>
          <div class="buttons">
           <div class="cancel" @click="cancel">取消</div>
           <div class="next" @click="nextStep">确认</div>
          </div>
        </div>
        <div class="pay-method" v-if="activeStep === 1">
          <div class="pay-imme">
            <h3><span class="el-icon-arrow-left align-left float-left" @click="prevStep"></span>订单支付</h3>
            <h2>￥{{totalCount}} </h2>
            <div class="pay-mes">
              <div>
                <span class="float-left">收款方：</span>
                <span class="float-right">宜春季季红火锅店</span>
              </div>
              <div>
                <span class="float-left">商品:</span>
                <span class="float-right">订单支付</span>
              </div>
            </div>
            <div class="real-pay" @click="simulatePay">
              确认支付
            </div>
          </div>
          <!-- <div class="prev" @click="prevStep">上一步</div> -->
        </div>
        <div class="call-waiter" v-if="activeStep === 2">
            已呼叫服务员
        </div>
        <div class="call-waiter" v-if="activeStep === 3">
            支付成功,欢迎下次光临
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {Loading} from 'element-ui'
export default {
  name: 'meMain',
  mounted () {
    this.$root.eventHub.$on('submitOrder', (yourData) => {
      console.log(yourData)
      if (yourData) {
        // console.log(yourData)
        if (yourData[0] > 0) {
          console.log(1)
          this.totalCount += yourData[0]
          this.list.push(yourData[1])
        }
        // this.list.forEach((item))
        // this.excOrder()
      }
    })
  },
  data () {
    return {
      msg: '我的主页',
      order: [],
      list: [],
      totalCount: 0,
      showPay: false,
      activeStep: 0,
      payMethod: '支付宝',
      payMethods: [
        {method: '支付宝', icon: 'icon-zhanghao'},
        {method: '微信', icon: 'icon-zhanghao'},
        {method: '餐后现金 / 刷卡结账', icon: 'icon-zhanghao'}
      ],
      activeIndex1: 0,
      activeIndex2: 0
    }
  },
  methods: {
    // excOrder () {
    //   let arr = []
    //   this.order.forEach((item) => {
    //     // console.log(item)
    //     item.list.forEach((item2) => {
    //       let obj = {}
    //       obj.name = item2.name
    //       obj.price = item2.price
    //       obj.count = item2.count
    //       obj.image = item2.image
    //       arr.push(obj)
    //     })
    //   })
    //   console.log(arr)
    //   // this.list = arr
    // }
    changeIndex (index1, index2) {
      this.activeIndex1 = index1
      this.activeIndex2 = index2
    },
    changeMethod (index) {
      this.payMethod = this.payMethods[index].method
      console.log(this.payMethod)
    },
    nextStep () {
      if (this.payMethod === '支付宝' || this.payMethod === '微信') {
        this.activeStep = 1
      } else {
        this.activeStep = 2
        setTimeout(() => {
          this.simulatePay()
        }, 500)
      }
    },
    cancel () {
      this.showPay = false
    },
    prevStep () {
      this.activeStep = 0
    },
    payOrder () {
      this.showPay = true
      this.activeStep = 0
      this.payMethod = '支付宝'
    },
    simulatePay () {
      let loadingInstance = Loading.service({
        lock: true,
        text: 'Loading'
      })
      setTimeout(() => {
        loadingInstance.close()
        this.list = []
        this.totalCount = 0
        this.activeStep = 3
        setTimeout(() => {
          loadingInstance.close()
          this.showPay = false
        }, 1000)
      }, 500)
    }
  },
  watch: {
    'list': {
      handler: function (newVal, oldVal) {
        console.log(newVal)
        // newVal.forEach((item, index, array) => {
        //   array.forEach((item2, index2, array2) => {
        //     if (item.name === item2.name && index !== index2) {
        //       item.count.push(item2.count[0])
        //       delete array2[index2]
        //     }
        //   })
        // })
      },
      deep: true
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  @import '../assets/styles/variable';
  .main-body{
    width: 100%;
    height: 100%;
    overflow:hidden;
    .main{
      width: 100%;
      height: 100%;
      display:flex;
      flex-direction:column;
      .header-title{
        height:40px;
        line-height:40px;
        font-size:20px;
        font-weight:900;
        border-bottom:1px solid #ddd;
      }
      .carousel{
        height:120px;
        line-height:40px;
        width: 100%;
        text-align:center;
        position: relative;
        // font-size:10px;
        overflow:hidden;
        .inside-msg{
          position:absolute;
          background-color: rgba(7,17,27,0.5);
          height:100%;
          width:100%;
          left:0px;
          top:0px;
          color:#fff;
          z-index:2;
          h3{
            margin:0px;
          }
        }
        .wrapper{
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          -webkit-filter: blur(5px);
          filter: blur(5px);
          z-index: 1;
          overflow:hidden;
          background: url('/static/img/季季红背景.jpg') center center;
          background-size: 100% auto;
          img{
            width:100%;
          }
        }
      }
      .order{
        flex:1;
        overflow:auto;
        padding:5px 0;
        .goods{
          position: relative;
          // padding-right:20px;
          box-sizing:border-box;
          overflow:hidden;
          margin-bottom:5px;
          // border-bottom:1px dashed $themeColor;
          .good{
            background-color:  rgba(7,17,27,0.15);
            font-size:12px;
            overflow:hidden;
            // margin-bottom: 2px;
            height:60px;
            line-height:30px;
            width:100%;
            display:flex;
            flex-direction:row;
            .img{
              width:70px;
              height:60px;
              display:flex;
              flex-direction:row;
              justify-content:flex-start;
              align-items:center;
              // box-sizing:border-box;
              // padding:5px;
              img{
                width:50px;
                height:50px;
                padding:0 10px;
                vertical-align:middle;
              }
            }
            .msg{
              flex:1;
              padding: 0 10px;
              display:flex;
              flex-direction:column;
              justify-content:flex-start;
              .count{
                text-align:left;
              }
            }
          }
          .active{
            // transform:scale(1, 1);
            height:60px;
            transition: all .5s linear;
            // background-color: red;
          }
        }
        .good::after{
          position: absolute;
          content:'';
          top:-15px;
          left:50%;
          margin-left:-10px;
          width:20px;
          height:20px;
          border-radius:50%;
          background-color: #fff;
        }
        .good::before{
          position: absolute;
          content:'';
          bottom:-15px;
          left:50%;
          margin-left:-10px;
          width:20px;
          height:20px;
          border-radius:50%;
          background-color: #fff;
        }
      }
      .total-count{
        height:50px;
        line-height:50px;
        text-align:center;
        background-color: rgba(7,17,27,0.35);
        color:#fff;
        display:flex;
        .count{
          // border-top:1px solid #999;
          // font-size:20px;
          // font-weight:900;
          flex:1;
          // color:$themeColor;
        }
        .pay-order{
          font-size:15px;
          width:135px;
          // background-color: $themeColor;
          border-left:1px solid #fff;
        }
      }
      .add{
        .good{
          .msg{
            .count{
              color:$themeColor;
            }
          }
        }
      }
    }
    .pay-for{
      position: absolute;
      left:0;
      right:0;
      top:0;
      bottom:0;
      background-color: transparent;
      .pay-main{
        width:90%;
        height:320px;
        margin:40% auto;
        background-color: #fff;
        box-shadow:0 2px 12px 0 rgba(0,0,0,0.1);
        padding:10px;
        box-sizing:border-box;
        .pay-method{
          transition:all 0.5s;
          .pay-message{
            padding:5px 0;
            height: 40px;
            line-height: 40px;
            border-bottom:1px dashed #eee;
            width:100%;
          }
          .flex-pay{
            // background-color: #a8abaf;
            height:150px;
            line-height:50px;
            margin:5px 10px;
            padding:10px 0;
            display:flex;
            flex-direction:column;
            // background-color: #eee;
            .pay{
              flex:1;
              // border:1px solid rgba(7,17,27,0.35);
              border-radius:2px;
              margin: 0 5px;
              text-align:left;
              div{
                float:right;
                width:20px;
                height:20px;
                font-size:20px;
                margin:15px;
                border-radius:50%;
                background-color: #fff;
              }
            }
            .activePay{
              // border-left-width:10px;
              // border-left-style:solid;
              // border-left-color:$themeColor;
              // border:1px solid #dcdfe6;
              span{
                color:$themeColor;
              }
            }
          }
          .buttons{
            background-color: $themeColor;
            display:flex;
            flex-direction:row;
            margin:5px 10px;
            padding:10px 0;
            color: #fff;
            .cancel{
              flex:1;
              .el-button{
                width:60%;
              }
            }
            .next{
              flex:1;
              border-left:1px solid #fff;
              .el-button{
                width:60%;
              }
            }
          }
          .pay-imme{
            line-height:1.7;
            h3{
              // border-bottom:1px dashed rgba(7,17,27,0.35);
              padding-bottom:10px;
              line-height:41px;
              span{
                font-size:20px;
                margin-top:10px;
              }
            }
            h2{
              padding-bottom:10px;
            }
            .pay-mes{
              div{
                width:100%;
                height:30px;
                line-height:30px;
              }
            }
            .real-pay{
              height:40px;
              margin-top:10px;
              line-height:40px;
              background-color: $themeColor;
              color: #fff;
            }
          }
        }
        .call-waiter{
          line-height: 160px;
          font-size:20px;
        }
      }
    }
  }
</style>



